import React, { Component } from 'react';
import { Navbar, Nav, Collapse, NavItem, Jumbotron } from 'reactstrap';
import { NavLink } from 'react-router-dom';//automatically adds active 

class Header extends Component {
    constructor(props){
        super(props);
        this.state = {
            isNavOpen: false
        };
        this.toggleNav = this.toggleNav.bind(this);
    }

    toggleNav() {
        this.setState({
            isNavOpen: !this.state.isNavOpen
        });
    }

    

    render() {
        return(
            <>
                <Navbar dark expand = "md">
                    <div className = "container">
                        <Collapse isOpen = {this.state.isNavOpen} navbar>
                        <Nav navbar className = "navbar-nav ml-auto">
                            <NavItem>
                                <NavLink className = "nav-link" to = "/home">
                                    <span></span> Home
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className = "nav-link" to = "/things">
                                    <span></span> People Speak
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className = "nav-link" to = "/memes">
                                    <span></span> Memes
                                </NavLink>
                            </NavItem>
                        </Nav>
                        </Collapse>
                    </div>
                </Navbar>
                <Jumbotron>
                    <div className = "container">
                            <h1 id = "birthday">Happy Birthday Shashank!</h1>
                    </div>
                </Jumbotron>
            </>
        );
    }
}

export default Header;

